/* Global Styles */
body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
}

/* Typography */
h1,
h2{
  font-weight: 600;
  text-align: center;
}

/* Buttons */
button {
  display: inline-block;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background-color: #ffcdd2;
  color: #000;
  font-size: 16px;

}

button:hover {
  background-color: #ff9a9e;
}

/* Form Styles */
form {
  margin-bottom: 20px;
}

label {
  display: block;
  font-weight: 500;
  margin-bottom: 5px;
}

input {
  width: 10%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}



/* Navigation Styles */
.navbar {
  background-color: #ffebee;
  color: #ffffff;
  text-align: center;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.navbar li {
  display: inline-block;
  margin-right: 10px;
}

.navbar a {
  display: block;
  padding: 10px;
  color: #000;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.navbar a:hover {
  background-color: #ffcdd2;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .container {
    padding: 10px;
  }

  .navbar {
    padding: 10px;
  }

  .navbar li {
    display: block;
    margin-bottom: 10px;
  }
}